html {
  text-decoration: none;
}/* COLORS *//* FONTS *//*BREAKPOINTS*/.container_logoCards {
  width: 100%;
  height: 111px;
  background: #ffffff;
  padding: 31px 79px 30px 40px;
  display: flex;
  justify-content: left;
  align-items: center;
}

.container_logoCards .logoCards {
  width: 201px;
}

.page {
  height: 100vh;
  font-size: 16px;
}

#colorBlindSVG {
  display: none;
}

.container_logo {
  background: #f5f5f5;
  width: 100%;
  padding: 95px 35px 35px 35px;
  display: flex;
  justify-content: center;
}

.container_logo .logo {
  width: 250px;
}

h1 {
  margin: 0px;
  padding: 0px;
}

.landing__container {
  width: 100%;
  height: 85%;
  background: #f5f5f5;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 26px;
  margin-right: 26px;
}

.main .main__title {
  width: 223px;
  height: 24px;
  font-family: "Merriweather", serif;
  font-size: 19.2px;
  font-weight: 900;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #e17334;
}

.main .main__content {
  width: 264.8px;
  height: 38.4px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 12.8px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #114E4E;
  padding: 0px;
  margin: 0px;
}

.main .main__icons {
  display: grid;
  grid-template-columns: 90px 90px 90px;
  grid-template-rows: 73px;
  text-align: center;
  border: solid 1.6px rgba(225, 115, 52, 0.21);
  border-radius: 4px;
  width: 268px;
  height: 73.8px;
  margin-top: 17.8px;
}

.main .main__icons i {
  color: #e17334;
  height: 26px;
  width: 32px;
  font-size: 22.4px;
  text-align: center;
  margin-top: 15px;
}

.main .main__text--icons {
  font-size: 12.8px;
  font-family: "Open Sans", sans-serif, sans-serif;
  color: #54585A;
  text-align: center;
}

.main p {
  display: block;
  padding: 0px;
  margin: 0px;
}

.main__button {
  width: 129px;
  height: 42px;
  border-radius: 21px;
  background-color: #438792;
  margin-top: 40px;
  margin-bottom: 30px;
}

.main__button a {
  display: block;
  margin-bottom: 30px;
  background: #438792;
  min-height: 42px;
  width: 129px;
  border-radius: 21px;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
}

.preview {
  background: url("../images/shiny.jpg") top center no-repeat;
  background-size: cover;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.preview .reset {
  width: 75px;
  height: 30px;
  margin-top: 20px;
  margin-bottom: 20px;
  align-self: center;
  transform: translateX(-85px);
  border: solid 1px #ffffff;
  border-radius: 15px;
  background-color: transparent;
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  line-height: 1.67;
  color: #54585a;
  text-transform: uppercase;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.preview .reset:hover {
  border: 2px solid #114E4E;
}

.preview .card {
  height: 400px;
  width: 270px;
  border-radius: 2px;
  background-color: rgba(17, 78, 78, 0.8);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px 30px 40px 30px;
  background-color: #ffffff;
  width: 240px;
  height: 329px;
}

.preview .card .headline {
  border-left: 5px solid #114E4E;
  align-self: flex-start;
  margin-top: 23px;
  margin-bottom: 10px;
  align-items: center;
}

.preview .card .headline .name {
  text-transform: capitalize;
  font-family: "Montserrat", sans-serif;
  font-weight: "Montserrat", sans-serif;
  font-size: 1.125rem;
  line-height: 20px;
  color: #114E4E;
  padding-left: 20px;
  margin-top: 0px;
  margin-bottom: 2px;
}

.preview .card .headline .role {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #54585A;
  padding-left: 20px;
  margin-top: 2px;
  margin-bottom: 0px;
}

.preview .card .image {
  background: url("../images/queen.gif") top center no-repeat;
  background-size: cover;
  width: 240px;
  height: 200px;
}

.socialKit {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 4px;
  height: 50px;
  width: 100%;
  padding-right: 62px;
  padding-left: 20px;
  margin-top: 7px;
}

.socialKit .list {
  height: 32px;
  opacity: 0.6;
  list-style: none;
}

.socialKit .list .btn {
  border: solid 2px green;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  margin: 0px 5px 15px 5px;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer {
  font-size: 16px;
}

footer .footer-wrapper {
  background-color: #ffffff;
  border-top: solid 1px #d8d8d8;
  height: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

footer .footer-wrapper .copyright {
  color: #6b6b6b;
  font-family: "Open Sans", sans-serif;
  line-height: 1.43;
  font-size: 0.875rem;
  margin-top: 18px;
}

footer .footer-wrapper .copyright .first-letter {
  text-transform: capitalize;
}

footer .footer-wrapper .img-container {
  height: 2.5rem;
  margin: 0.75rem;
}

footer .footer-wrapper .img-container img {
  height: 100%;
}

@media all and (min-width: 768px) {
  .container_logo {
    padding: 250px 174px 31px 175px;
  }

  .container_logo .logo {
    width: 419px;
  }

  .landing__container {
    height: 90%;
  }

  .main .main__title {
    font-size: 24px;
    width: 278px;
    height: 31px;
  }

  .main .main__content {
    width: 331px;
    height: 48px;
    font-size: 16px;
  }

  .main .main__icons {
    grid-template-columns: 108px 108px 108px;
    grid-template-rows: 86px;
    width: 325px;
    height: 86px;
  }

  .main .main__icons i {
    height: 29px;
    font-size: 28px;
  }

  .main .main__text--icons {
    font-size: 16px;
  }

  .main__button {
    margin-top: 11.63rem;
    margin-bottom: 3.68rem;
  }

  footer .footer-wrapper {
    height: 10%;
    padding: 10px 40px;
    flex-direction: row;
    justify-content: space-between;
  }

  footer .footer-wrapper .copyright {
    margin-top: 0px;
  }
}

@media all and (min-width: 1200px) {
  .container_logo {
    padding: 180px 174px 31px 175px;
  }
}
