.page {
  height: 100vh;
  font-size: 16px;
}

#colorBlindSVG {
  display: none;
}
.container_logo {
  background: #f5f5f5;
  width: 100%;
  padding: 95px 35px 35px 35px;
  display: flex;
  justify-content: center;

  @include forTablet {
    padding: 250px 174px 31px 175px;
  }
  
  @include forPc {
    padding: 180px 174px 31px 175px;
  }

  .logo {
    width: 250px;

    @include forTablet {
      width: 419px;
    }
  }
}
h1 {
  margin: 0px;
  padding: 0px;
}
.landing__container {
  width: 100%;
  height: 85%;
  background: #f5f5f5;
  @include forTablet {
    height: 90%;
  }
}
.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 26px;
  margin-right: 26px;

  .main__title {
    width: 223px;
    height: 24px;
    font-family: $landingFont;
    font-size: 19.2px;
    font-weight: 900;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: $primaryColor;
    @include forTablet {
      font-size: 24px;
      width: 278px;
      height: 31px;
    }
  }

  .main__content {
    width: 264.8px;
    height: 38.4px;
    @include primaryBoldFont;
    font-size: 12.8px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: $headerColor;
    padding: 0px;
    margin: 0px;
    @include forTablet {
      width: 331px;
      height: 48px;
      font-size: 16px;
    }
  }

  .main__icons {
    display: grid;
    grid-template-columns: 90px 90px 90px;
    grid-template-rows: 73px;
    text-align: center;
    border: solid 1.6px rgba(225, 115, 52, 0.21);
    border-radius: 4px;
    width: 268px;
    height: 73.8px;
    margin-top: 17.8px;
 

    @include forTablet {
      grid-template-columns: 108px 108px 108px;
      grid-template-rows: 86px;
      width: 325px;
      height: 86px;
    }

     i {
      color: #e17334;
      height: 26px;
      width: 32px;
      font-size: 22.4px;
      text-align: center;
      margin-top: 15px;

      @include forTablet {
        height: 29px;
        font-size: 28px;
      }
    }
  }

  .main__text--icons {
    font-size: 12.8px;
    font-family: $primaryFont, sans-serif;
    color: $metalColor;
    text-align: center;
  
    @include forTablet {
      font-size: 16px;
    }
  }
  & p {
    display: block;
    padding: 0px;
    margin:0px;
   
  }
}

.main__button {
  width: 129px;
  height: 42px;
  border-radius: 21px;
  background-color: $bottonColor;
  margin-top: 40px;
  margin-bottom: 30px;
  @include forTablet {
    margin-top: 11.63rem;
    margin-bottom:3.68rem ;
  }

  & a {
    display: block;
    margin-bottom: 30px;
    background: #438792;
    min-height: 42px;
    width: 129px;
    border-radius: 21px;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    align-items: center;
    font-family: "Open Sans";
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
  }
}

